import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { DsaRegisterComponent } from './auth/dsa-register/dsa-register.component';
import { EmailConfirmComponent } from './auth/email-confirm/email-confirm.component';
import { ResetPasswordComponent } from './auth/reset-password/reset-password.component';
import { SignInComponent } from './auth/sign-in/sign-in.component';
import { SignupComponent } from './auth/signup/signup.component';
import { MaintenanceComponent } from './pages/maintenance/maintenance.component';


const routes: Routes = [
    {
        path: 'home',
        loadChildren: () => import('./pages/home/home.module').then(m => m.HomeModule)
    },
    {
        path: 'my-accounts', loadChildren: () => import('./pages/my-accounts/my-accounts.module').then(m => m.MyAccountsModule)
    },
    {
        path: 'other-settings', loadChildren: () => import('./pages/other-settings/other-settings.module').then(m => m.OtherSettingsModule)
    },
    {
        path: 'dsa-module', loadChildren: () => import('./pages/dsa-module/dsa-module.module').then(m => m.DsaModuleModule)
    },
    {
        path: 'signup',
        component: SignupComponent
    }, {
        path: 'signin',
        component: SignInComponent
    },{
        path: 'dsa-buyer-signup',
        component: SignupComponent
    },
    {
        path: 'dsa-signup', component: DsaRegisterComponent
    },
    {
        path: 'dsc-create-dsa', component: DsaRegisterComponent
    },
    {
        path: 'forgot-password',
        component: EmailConfirmComponent
    },
    {
        path: 'reset-password',
        component: ResetPasswordComponent
    },
    {
        path: 'maintenance',
        component: MaintenanceComponent
    },
    {
        path: '',
        redirectTo: '/home',
        pathMatch: 'full'
    },
];

@NgModule({
    imports: [RouterModule.forRoot(routes, { scrollPositionRestoration: 'enabled', initialNavigation: 'enabledBlocking' })],
    exports: [RouterModule]
})
export class AppRoutingModule { }
