import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { Router } from '@angular/router';

@Injectable({
    providedIn: 'root'
})
export class SettingService {

    baseUrl = environment.api1;
    apiUrl = environment.api3;

    constructor(
        private http: HttpClient,
        private router: Router
    ) { }

    setOTP(data: any) {
        return this.http.post(this.baseUrl + 'set-otp', data);
    }
    
    setOTPChangeEmail(data: any) {
        return this.http.post(this.baseUrl + 'set-otp-change-email', data);
    }

    updatePassword(data: any) {
        return this.http.post(this.baseUrl + 'update-passwords', data);
    }

    changeEmail(data: any) {
        return this.http.post(this.baseUrl + 'change-email', data);
    }

    // Notification preference Api
    get_notification_pref(id: any) {
        return this.http.post(this.apiUrl + 'get-notification-preferences', { id: id });
    }

    create_notification_pref(data: any) {
        return this.http.post(this.apiUrl + 'create-notification-preferences', data);
    }


    // menu permission
    

    getMenuPermission(data: any) {
        return this.http.post(environment.api5 + 'get-menu-permission', data);
    }

    getPermission(data: any) {
        data.formName = this.router.url.split('?')[0];
        return this.http.post(environment.api5 + 'get-permission', data);
    }

    // *** //


    //  code domain Api
    getCodeDomainVal(code: any){
        return this.http.post(environment.api5 + 'get-codedomain-value', {code});
    }

    inquiryEmail(data: any){
        return this.http.post(environment.api5 + 'buyer-enquiry', data);
    }
}
