    public loadScript() {
        let node = document.createElement('script');
        node.src = 'assets/js/main.js';
        node.type = 'text/javascript';
        node.async = true;
        node.charset = 'utf-8';
        document.getElementsByTagName('head')[0].appendChild(node);
    }

    // fownload file
    downloadFileFromUrl(url: any, filename: any) {
        fetch(url).then(function (t) {
            return t.blob().then((b) => {
                var a = document.createElement("a");
                a.href = URL.createObjectURL(b);
                a.setAttribute("download", filename);
                a.click();
            });
        });
    }

    readMore() {
        var dots = <HTMLElement>document.getElementById("dots");
        var moreText = <HTMLElement>document.getElementById("more");
        var btnText = <HTMLElement>document.getElementById("myBtn");

        if (dots.style.display === "none") {
            dots.style.display = "inline";
            btnText.innerHTML = "Read more";
            moreText.style.display = "none";
        } else {
            dots.style.display = "none";
            btnText.innerHTML = "Read less";
            moreText.style.display = "inline";
        }
    }
