import { DatePipe } from '@angular/common';
import { HttpClientModule } from '@angular/common/http';
import { NgModule, CUSTOM_ELEMENTS_SCHEMA, NO_ERRORS_SCHEMA } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { BrowserModule } from '@angular/platform-browser';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { SignInComponent } from './auth/sign-in/sign-in.component';
import { SignupComponent } from './auth/signup/signup.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ToastrModule } from 'ngx-toastr';
import { EmailConfirmComponent } from './auth/email-confirm/email-confirm.component';
import { SharedModule } from './shared/shared.module';
import { OAuthModule, AuthConfig, ValidationHandler, OAuthStorage, OAuthModuleConfig } from 'angular-oauth2-oidc'; // Added
import { AuthInterceptor } from './auth/auth.interceptor';
import { Ng2TelInputModule } from 'ng2-tel-input';
import { PasswordStrengthMeterModule } from 'angular-password-strength-meter';
import { NgOtpInputModule } from 'ng-otp-input';
import { ResetPasswordComponent } from './auth/reset-password/reset-password.component';
import { LightboxModule } from 'ngx-lightbox';
import { DsaRegisterComponent } from './auth/dsa-register/dsa-register.component';
import { AuthGuard } from './Guard/auth.guard';
import { Auth2Guard } from './Guard/auth2.guard';
import { LoginGuard } from './Guard/login.guard';
import { PrivacyPolicyComponent } from './pages/privacy-policy/privacy-policy.component';
import { TermsConditionComponent } from './pages/terms-condition/terms-condition.component';
import { MaintenanceComponent } from './pages/maintenance/maintenance.component';

const config: AuthConfig = {
    issuer: window.location.origin,
    clientId: window.location.origin,
    redirectUri: window.location.origin + '/index.html',
    logoutUrl: window.location.origin + '/login',
    // silentRefreshRedirectUri: window.location.origin + '/silent-refresh.html',
    scope: 'openid profile email',
    requireHttps: false
};

config.logoutUrl = `${config.issuer}v2/logout?client_id=${config.clientId}&returnTo=${encodeURIComponent(config.redirectUri || '')}`;

const authModuleConfig: OAuthModuleConfig = {
    resourceServer: {
        allowedUrls: ['http://localhost:4200'],
        sendAccessToken: true,
    },
};

@NgModule({
    declarations: [
        AppComponent,
        SignInComponent,
        SignupComponent,
        DsaRegisterComponent,
        EmailConfirmComponent,
        ResetPasswordComponent,
        PrivacyPolicyComponent,
        TermsConditionComponent,
        MaintenanceComponent
    ],
    imports: [
        BrowserModule.withServerTransition({ appId: 'serverApp' }),
        AppRoutingModule,
        FormsModule,
        HttpClientModule,
        BrowserAnimationsModule, // required animations module
        ToastrModule.forRoot({
            preventDuplicates: true
        }), // ToastrModule added
        SharedModule,
        OAuthModule.forRoot(authModuleConfig),
        Ng2TelInputModule,
        PasswordStrengthMeterModule,
        NgOtpInputModule,
        LightboxModule
    ],
    providers: [
        AuthGuard,
        Auth2Guard,
        LoginGuard,
        // {
        //   provide: HTTP_INTERCEPTORS,
        //   useClass: AuthInterceptor,
        //   multi: true
        // },
        { provide: OAuthModuleConfig, useValue: authModuleConfig },
        // { provide: ValidationHandler, useClass: JwksValidationHandler },
        { provide: OAuthStorage, useValue: localStorage },
        { provide: AuthConfig, useValue: config },
        DatePipe],
    schemas: [NO_ERRORS_SCHEMA, CUSTOM_ELEMENTS_SCHEMA],
    bootstrap: [AppComponent]
})
export class AppModule { }
